body {
  font-family: "Google Sans", Roboto, Arial, Helvetica, sans-serif; }

.main-page-content {
  min-height: 600px;
  margin-bottom: 100px; }

.navigation {
  padding: 10px; }
  @media (min-width: 601px) {
    .navigation {
      padding: 30px 5%; } }
  .navigation ul.items {
    margin: 0; }
    .navigation ul.items li {
      display: inline-block;
      padding: 10px; }
      .navigation ul.items li a {
        color: #323232;
        transition: all 500ms; }
        .navigation ul.items li a:after {
          position: relative;
          width: 10px;
          height: 1px;
          background: #039be5;
          display: block;
          content: ""; }
        .navigation ul.items li a:hover {
          color: #039be5; }
          .navigation ul.items li a:hover:after {
            width: 100%; }
    .navigation ul.items li.brand {
      padding: 0px;
      float: right; }
      .navigation ul.items li.brand a:after {
        display: none; }
      .navigation ul.items li.brand a img.logo {
        width: 32px; }
        @media (min-width: 601px) {
          .navigation ul.items li.brand a img.logo {
            width: 52px; } }

.page-footer {
  background-color: #f8f9fa;
  padding: 20px;
  position: relative; }
  .page-footer .logo {
    width: 60px;
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    top: -30px;
    border-radius: 50%; }
  .page-footer .container {
    border-top: 1px solid #cccccc;
    margin-top: 50px; }
    .page-footer .container .row {
      margin-bottom: 0px;
      margin-top: 20px; }
      .page-footer .container .row .col a.quick-links {
        padding: 10px;
        display: inline-block;
        color: #323232;
        transition: all 500ms; }
        .page-footer .container .row .col a.quick-links:after {
          position: relative;
          width: 20px;
          height: 1px;
          background: #039be5;
          display: block;
          content: ""; }
        .page-footer .container .row .col a.quick-links:hover {
          color: #039be5; }
          .page-footer .container .row .col a.quick-links:hover:after {
            width: 100%; }
        .page-footer .container .row .col a.quick-links i {
          vertical-align: middle;
          font-size: 16px;
          padding-right: 3px;
          color: #fd9826; }
  .page-footer .reg-details {
    margin: 40px 0 0 0;
    text-align: center; }
    .page-footer .reg-details span {
      display: block; }
      @media (min-width: 601px) {
        .page-footer .reg-details span {
          display: inline-block;
          padding-right: 20px; } }

.contact-us .address {
  max-width: 440px;
  margin: 10px auto 10px auto;
  background: #f8f9fa;
  color: #fd9826; }
  @media (min-width: 601px) {
    .contact-us .address {
      max-width: 520px; } }
  .contact-us .address .card-title {
    color: #039be5; }

.contact-us .person {
  max-width: 310px;
  margin: 10px auto 10px auto; }
  .contact-us .person .card-image img {
    border-radius: 50%;
    padding: 30px; }
  .contact-us .person .card-image .card-title {
    background: rgba(255, 255, 255, 0.45098);
    right: 0;
    padding: 10px 5px;
    color: #323232; }
  .contact-us .person .card-content p {
    font-family: "Reenie Beanie", cursive;
    font-size: 24px;
    line-height: 1.2;
    color: #039be5; }
  .contact-us .person .card-action a.phone_email {
    text-transform: lowercase !important; }
